import React from 'react'
import LandingPageBar from './LandingPageBar'

import '../../css/landing-page.css';

import Button from '../Buttons/Button';
import CircleButton from '../Buttons/CircleButton';
import DarkButton from '../Buttons/DarkButton';
import GreenBorder from '../Borders/GreenBorder';
import CardOpen from '../Borders/CardOpen';
import CardClosed from '../Borders/CardClosed';
import CardBack from '../Borders/CardBack';


export default function LandingPage(props) {
    return (
        <div>
            <LandingPageBar openMenu={props.openMenu} />
            <div>
                <CardBack>
                    <div className="button-bar">
                        <Button>Sign up</Button>
                        <Button>Collections</Button>
                        <Button>Seasonal</Button>
                    </div>
                </CardBack>
            </div>
            <div>
                <CardClosed>
                    <CircleButton>SALE <subtitle>cards</subtitle> </CircleButton>
                </CardClosed>
            </div>
            <div>
                <CardOpen>
                     <div className="button-bar">
                        <DarkButton>Sign up</DarkButton>
                        <DarkButton>Collections</DarkButton>
                        <DarkButton>Seasonal</DarkButton>
                    </div>
                </CardOpen>
            </div>
            
        </div>
    )
}
