import React from 'react';
import closeMenuImg from '../images/CloseMenu-02.png';

import '../css/menu.css';

export default function Menu(props) {
    return (
        <div className={'menu' + (props.open ? ' open' : '')}>
            <div onClick={props.closeMenu} className="close-menu-button">
                <img src={closeMenuImg} />
            </div>
            <div className="menu-links">
                <a>Home</a>
                <a>Collections</a>
                <a>Sign Up</a>
                <a>About</a>
            </div>
        </div>
    )
}
